@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.fadeIn {
  animation: fadeIn 400ms ease-in-out; }

@keyframes slideLeft {
  0% {
    opacity: 0;
    transform: translateX(100%); }
  100% {
    opacity: 1;
    transform: none; } }

.slideLeft {
  animation: slideLeft 400ms ease-in-out; }

@keyframes slideRight {
  0% {
    opacity: 0;
    transform: translateX(-100%); }
  100% {
    opacity: 1;
    transform: none; } }

.slideRight {
  animation: slideRight 400ms ease-in-out; }
